.intro, .about, .about__content, .top-section, .top-section__header-changeslide, .top-section__header-deskNav, .top-section__header-deskNav-list, .top-section__header-mobNav, .top-section__header-mobNav-list, .top-section__hero, .top-section__hero-wrapper-slider {
